@import url("https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100;8..144,400;8..144,700&family=Roboto+Mono:wght@100;400;700&family=Roboto+Slab:wght@100;400;700&family=Roboto:wght@100;400;700&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* modern-normalize v2.0.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*,
::before,
::after {
  box-sizing: border-box;
}
html {
  font-family: system-ui, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -moz-tab-size: 4;
  tab-size: 4;
}
body {
  margin: 0;
}
hr {
  height: 0;
  color: inherit;
}
abbr[title] {
  text-decoration: underline dotted;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp,
pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, 'Liberation Mono', Menlo, monospace;
  font-size: 1em;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
table {
  text-indent: 0;
  border-color: inherit;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
select {
  text-transform: none;
}
button,
[type='button'],
[type='reset'],
[type='submit'] {
  -webkit-appearance: button;
}
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
:-moz-focusring {
  outline: 1px dotted ButtonText;
}
:-moz-ui-invalid {
  box-shadow: none;
}
legend {
  padding: 0;
}
progress {
  vertical-align: baseline;
}
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}
[type='search'] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
summary {
  display: list-item;
}
html,
body {
  height: 100%;
  overflow: inherit;
}
* html,
* body {
  min-height: 100%;
}
* html #off-canvas {
  height: 100%;
}
#off-canvas {
  display: grid;
  min-height: 100%;
  grid-template-rows: auto 1fr auto;
}
form,
label,
input,
input[type=text],
input[type=email],
input[type=url],
input[type=password],
select,
textarea {
  max-width: 100%;
  resize: vertical;
}
html,
body,
* {
  font-family: 'Roboto', sans-serif, ui-sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto Slab', serif, ui-serif;
  text-wrap: balance;
}
code,
pre {
  font-family: 'Roboto Mono', monospace, ui-monospace;
}
h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.5em;
}
h3 {
  font-size: 1.17em;
}
h4 {
  font-size: 1em;
}
h5 {
  font-size: 0.83em;
}
h6 {
  font-size: 0.67em;
}
body.toolbar-horizontal #off-canvas {
  min-height: calc(100dvh - 60px);
}
body.toolbar-vertical #off-canvas {
  min-height: calc(100dvh - 49px);
}
body.maintenance-page #off-canvas {
  place-items: center;
  grid-template-rows: auto;
}
.wrapper {
  --width__default: 2033px;
  width: min(var(--width__default), 100% - 1.25rem);
  margin-inline: auto;
}
a.feed-icon {
  overflow: hidden;
  display: block;
  max-width: 1.3rem;
  max-height: 1.3rem;
  margin-top: 0.75rem;
}
.site-header__nav-wrapper .menu-button {
  position: absolute;
  visibility: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
.form-item {
  display: flex;
  flex-direction: column;
}
.block-system-branding-block {
  display: flex;
  align-items: center;
}
.block-system-branding-block .site-branding__logo-image {
  max-width: 5rem;
}
textarea {
  width: 100%;
}
.copyright-footer {
  margin-bottom: 1.25rem;
}
section.site-content {
  line-height: 1.25;
}
footer {
  padding-block: 1.25rem;
  min-height: 4.2rem;
}
footer .region--footer {
  display: grid;
}
footer .region--footer div:nth-child(2),
footer .region--footer div:last-child {
  grid-column: 1/2;
}
footer .region--footer div:nth-child(2) {
  grid-row: 1/2;
}
footer .region--footer div:last-child {
  grid-row: 2/3;
}
footer .region--footer nav {
  grid-row: 1/-1;
  grid-column: 2/3;
}
footer .region--footer nav .menu {
  list-style: square;
}
header .site-header__inner {
  display: flex;
  column-gap: 1rem;
  flex-wrap: wrap;
}
header .site-header__inner .site-header__brand-wrapper,
header .site-header__inner .site-header__nav-wrapper {
  display: grid;
}
.with-title .page-title-block {
  margin-bottom: 0.5rem;
}
figure.hero {
  padding-inline: 0;
}
main {
  max-width: 100dvw;
}
main > figure,
main > section {
  max-width: 100%;
}
.local-tasks-block {
  margin-block: 0.5rem 0.75rem;
}
nav.local-tasks__primary,
nav.local-tasks__secondary {
  display: grid;
}
nav.local-tasks__primary ul {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1.25rem;
  max-width: max-content;
}
nav.local-tasks__primary ul li a {
  display: block;
  padding-block: 0.5rem;
}
.site-header__nav-wrapper .region--navigation nav ul.menu {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(6rem, 1fr));
  grid-auto-flow: column;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li {
  min-width: 6rem;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li a {
  display: grid;
  place-content: center;
  padding-block: 2rem;
}
.region--branding {
  display: grid;
}
.region--navigation {
  display: grid;
  justify-items: end;
}
body.maintenance-page header img {
  max-height: 55dvh;
}
